:root {
  --base: #ffffff;
  --base-o: #ffffff83;
  --base-special: #ccc5b9;
  --base-special-o: #ccc5b957;
  --base-special-two: #ffe4c4;
  --base-shadow: #252422;
  --text: #403d39;
  --text-special: #eb5e28;
  --text-shadow: #252422;
  --err: #c20114;
}

:root ::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--base-text);
  opacity: 1;
  /* Firefox */
}

:root :-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: var(--base-text);
}

:root ::-ms-input-placeholder {
  /* Microsoft Edge */
  color: var(--base-text);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--base);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#App {
  min-height: 100vh;
  position: relative;
  font-size: calc(10px + 0.5vw);
  overflow: hidden;
  position: relative;
}

@media screen and (min-width: 1000px) {
  #App {
    font-size: 15px;
  }
}

.about {
  width: 100%;
  max-width: 1000px;
  font-size: 1.2em;
  color: var(--text);
}

.about h1 {
  font-size: 3em;
}

.about section {
  position: relative;
  z-index: 2;
}

.about section:last-of-type {
  transition: all 1s;
  padding: 20px;
  margin-top: 50px;
  text-align: center;
}

.about section:last-of-type p {
  margin: 7px;
}

@media (max-width: 1000px) {
  .about {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .about section {
    width: 100%;
  }
}

.alpha {
  color: var(--text-special);
  font-weight: normal;
  filter: blur(4px);
  text-shadow: -50px -40px var(--text-special);
}

.crash:hover {
  visibility: hidden;
}

.crash:hover + span {
  color: var(--err);
  opacity: 0.5;
}

.hi {
  color: var(--text-special);
  font-weight: bold;
  font-size: 40px;
  font-size: 1.5em;
}

@keyframes hi {
  0% {
    color: var(--text);
    text-shadow: -5px -5px var(--text-shadow);
    filter: blur(4px);
  }
  50% {
    color: var(--text-special);
    filter: blur(1px);
  }
  100% {
  }
}

.block {
  display: flex;
  position: relative;
  z-index: 5;
  transition: background-color 1.5s;
  flex-direction: column;
  padding: 20px;
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
  color: var(--text);
}

.block:hover {
  background-color: var(--base-special-o);
  border-radius: 5px;
}

.block__text {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 100px;
  max-height: 500px;
  transition: min-height 1s, max-height 1s;
}

@media screen and (min-width: 1000px) {
  .block__text {
    width: 50%;
  }
}

.block__text__header {
  padding: 10px;
  opacity: 1;
}

.block__text__content {
  position: absolute;
  visibility: hidden;
  padding: 10px;
}

.block:hover img {
  opacity: 1;
}

.block--image {
  width: 100%;
}

@media screen and (min-width: 1000px) {
  .block--image {
    width: 50%;
  }
}

.block--image img {
  transition: all 1s;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 0 5px var(--base-shadow);
  border-radius: 5px;
  opacity: 0.5;
}

@media (max-width: 1000px) {
  .block--image {
    justify-content: flex-end;
    display: flex;
  }
}

@media (min-width: 1000px) {
  .block {
    flex-direction: row;
  }
}

.contact {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 20px;
  margin-top: 0;
  margin-bottom: 80px;
}

.contact h1 {
  margin-top: 0;
  margin-bottom: 1em;
  font-size: 3em;
  text-align: center;
  color: var(--text);
}

.contact__block {
  width: 500px;
  height: 50px;
  margin: 5px;
  font-size: 20px;
}

@media (max-width: 500px) {
  .contact__block {
    width: auto;
    height: auto;
    font-size: 13px;
  }
}

.footer {
  position: absolute;
  font-size: 3em;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  z-index: 3;
  bottom: 0;
}

.footer span {
  border-radius: 50%;
}

.footer--icon {
  cursor: pointer;
  margin: 10px;
  color: var(--text) !important;
}

.footer--icon:hover * {
  color: var(--text-special);
}

.contactForm {
  resize: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--text);
  z-index: 3;
}

.contactForm * {
  text-align: center;
  max-width: 1000px;
}

.contactForm label {
  font-size: 0px;
}

.contactForm p {
  font-size: 1.5em;
  color: var(--text-special);
}

.contactForm input,
.contactForm textarea {
  margin: 0 !important;
  padding: 5px !important;
  border: 2px solid var(--base-special-two) !important;
  background-color: var(--base-special);
  opacity: 0.8;
  width: 90%;
  resize: none;
  outline: none;
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  font-weight: 400 !important;
}

.contactForm input:hover,
.contactForm textarea:hover {
  opacity: 1;
}

.contactForm input:focus,
.contactForm textarea:focus {
  opacity: 1;
}

.contactForm input {
  height: 30px;
  border-radius: 5px 5px 0 0;
}

.contactForm textarea {
  border-radius: 0 0 5px 5px;
  height: 200px;
}

.contactForm button {
  margin-top: 2em;
  background-color: transparent;
  width: fit-content;
  align-self: center;
  justify-self: center;
  width: 100%;
  outline: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 1px solid var(--text);
  -webkit-text-fill-color: var(--text);
  -webkit-box-shadow: 0 0 0px 1000px var(--base-special-two) inset;
  transition: background-color 5000s ease-in-out 0s;
  opacity: 1;
}

.header {
  transition: all 0.5s;
  box-shadow: 0 0 5px var(--base-shadow);
  display: flex;
  position: fixed;
  top: 0;
  justify-content: space-between;
  height: 60px;
  z-index: 9;
  width: 100%;
  background-color: var(--base-o);
}

.header a {
  display: flex;
}

.header__logo {
  user-select: none;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 5px;
  margin-top: 5px;
  justify-content: flex-end;
}

.header__logo img {
  width: 70px;
}

.header__hamburger {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 5%;
  margin-bottom: 5px;
  margin-top: 5px;
}

@media screen and (min-width: 800px) {
  .header__hamburger {
    display: none;
  }
}

@media screen and (max-width: 799px) {
  .header__full {
    display: none;
  }
}

.header__full__buttons {
  width: 50%;
  height: 100%;
  min-width: 500px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.header__full__buttons--active {
  border-bottom: 1px solid var(--text-special);
  box-shadow: 0 4px 2px -2px var(--text-special);
  text-shadow: 0 0 2px var(--text-special);
}

.header__full__buttons--button {
  color: var(--text-special);
  font-weight: bold;
  transition: font-size 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: 1.5em;
  height: 100%;
  width: 100%;
}

.header__full__buttons--button:hover {
  font-size: 2em;
  border-bottom: 1px solid var(--text-special);
  box-shadow: 0 4px 2px -2px var(--text-special);
}

.header__full__buttons--button:active {
  border: unset;
  box-shadow: unset;
}

.home {
  width: 100%;
  max-width: 1000px;
  font-size: 1.2em;
  color: var(--text);
}

.home h1 {
  font-size: 3em;
}

.home__header {
  margin: 20px;
  position: relative;
  z-index: 3;
}

.home__header * {
  z-index: 3;
}

.home__header--title {
  font-size: 1.2em;
  font-weight: bold;
  opacity: 0.8;
  color: var(--text);
  margin: unset;
}

.home__header h1 {
  margin: 0px;
  font-size: 3em;
  color: var(--text-special);
  color: var(--text);
}

.home__header h1 * {
  transition: color 0.4s, text-shadow 0.5s;
}

.home__block {
  z-index: 2;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top: 150px;
  margin-bottom: 150px;
}

.home__block--animation {
  z-index: 2;
  width: 100%;
  padding: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 15s, filter 1s;
}

.home__block--animation div {
  opacity: 1;
  position: absolute;
  z-index: 2;
  content: "";
  width: 20em;
  height: 20em;
  max-width: 70vw;
  max-height: 70vw;
  background-color: var(--base-special);
  border-radius: 50%;
  animation: fly 4s ease, mite 20s linear 4s infinite;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: transparent;
  text-align: center;
  border: 0px double transparent;
  transition: all 1s, background-color 30s;
}

.home__block--animation div:hover {
  border: 25px double var(--base-special-two);
}

.home__block--animation div:hover::before {
  width: 0;
  height: 0;
  opacity: 0;
}

.home__block--animation div:active {
  transition: background-color 0.1s;
  background-color: var(--base-special-two);
}

.home__block--animation div::before {
  transition: all 1s, background-color 10s;
  border: 25px double var(--base-special);
  opacity: 1;
  position: absolute;
  content: "";
  display: flex;
  font-weight: bold;
  font-size: 1em;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 15em;
  height: 15em;
  max-width: 40vw;
  max-height: 40vw;
  background-color: var(--base-special-two);
  border-radius: 50%;
  animation: mite 30s linear infinite;
  animation: fly 2s ease, mite 80s linear 4s infinite;
}

.home__block--info {
  z-index: 3;
  max-width: fit-content;
  width: 100%;
  padding: 40px;
  border-left: 4px dashed var(--base-special);
  color: var(--text);
  font-weight: bold;
}

.home section {
  position: relative;
  z-index: 2;
}

.home section:last-of-type {
  transition: all 1s;
  padding: 20px;
  margin-top: 50px;
  text-align: center;
}

.home section:last-of-type p {
  margin: 7px;
}

@media (max-width: 700px) {
  .home__block {
    margin-top: 70px;
    margin-bottom: 20px;
    flex-direction: column;
  }
  .home__block--animation {
    display: none;
  }
}

@keyframes fly {
  0% {
    transform: rotate(-400deg) translateX(-30vh);
    opacity: 0.1;
    filter: blur(15px);
  }
  100% {
    transform: rotate(0deg) translateX(0);
    opacity: 1;
  }
}

@keyframes mite {
  0% {
    transform: rotate(0deg);
    border-radius: 50%;
  }
  50% {
    transform: rotate(180deg) translateX(5vh) skew(0, 5deg);
    border-radius: 40%;
  }
  100% {
    transform: rotate(360deg);
    border-radius: 50%;
  }
}

.project--info {
  background-color: var(--base-special-o);
}

.project--info .block__text {
  display: flex;
  flex-direction: column-reverse;
}

.project--info .block__text__header {
  opacity: 0 !important;
}

.project--info .block__text__content {
  animation: appear 1s linear;
  visibility: visible;
}

@keyframes disappear {
  0% {
    position: unset;
    opacity: 1;
  }
  100% {
    position: absolute;
  }
}

@keyframes appear {
  0% {
    visibility: hidden;
    opacity: 0;
  }
  50% {
    opacity: 0;
    visibility: visible;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}

.navigation {
  position: relative;
  background-color: transparent;
  outline: unset;
  border: none;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

.navigation--button {
  border-radius: 5px;
  margin: 20px 0px;
  width: 80%;
  max-width: 300px;
  min-width: fit-content;
  text-align: center;
  text-decoration: none;
  color: grey;
  font-size: 25px;
  transition: opacity 1s;
  box-shadow: 0 0 1px var(--base-shadow);
  position: relative;
  display: inline-block;
  padding: 0.8em 0.8em;
  cursor: pointer;
  overflow: hidden;
}

.navigation--button span:last-of-type {
  transition: opacity 1s;
}

.navigation--button:active {
  box-shadow: 0 0 4px var(--text-special);
}

.navigation--button:hover {
  color: var(--text-special);
  text-shadow: 0 0 1px var(--text-special);
}

.navigation--button:before, .navigation--button:after {
  content: "";
  position: absolute;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: var(--base-special);
}

.navigation--button:before {
  top: 0;
}

.navigation--button:after {
  bottom: 0;
}

.navigation--button:hover > * > *:before, .navigation--button:hover > * > *:after {
  transform: translate3d(0, 0, 0);
}

.navigation--button:hover > * > * > *:before, .navigation--button:hover > * > * > *:after {
  transform: translate3d(0, 0, 0);
}

.navigation--button > *:before, .navigation--button > *:after {
  content: "";
  position: absolute;
  top: 0;
  height: 100%;
  width: 2px;
  background-color: var(--base-special);
}

.navigation--button > *:before {
  left: 0;
}

.navigation--button > *:after {
  right: 0;
}

.navigation--button > * > *:before, .navigation--button > * > *:after {
  content: "";
  position: absolute;
  left: 0;
  z-index: 9;
  height: 2px;
  width: 100%;
  background-color: var(--text-special);
}

.navigation--button > * > *:before {
  top: 0;
  transform: translate3d(-105%, 0, 0);
  transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.navigation--button > * > *:after {
  bottom: 0;
  transform: translate3d(105%, 0, 0);
  transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.navigation--button > * > * > *:before, .navigation--button > * > * > *:after {
  content: "";
  position: absolute;
  top: 0;
  z-index: 9;
  height: 100%;
  width: 2px;
  background-color: var(--text-special);
}

.navigation--button > * > * > *:before {
  left: 0;
  transform: translate3d(0, 105%, 0);
  transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.navigation--button > * > * > *:after {
  right: 0;
  transform: translate3d(0, -105%, 0);
  transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.navigationPanel {
  user-select: none;
  margin: 80px 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  z-index: 3;
  justify-content: space-around;
}

.navigationPanel .navigation--button {
  margin: 20px;
  margin-bottom: 40px;
}

@media (min-width: 1000px) {
  .navigationPanel {
    flex-direction: row;
  }
  .navigationPanel .navigation::after {
    background-image: url(https://res.cloudinary.com/gregor1494/image/upload/v1611960377/portfolio/arrow_qwtx3u.svg);
    background-size: contain;
    background-repeat: no-repeat;
    content: "";
    min-height: -moz-fit-content;
    min-height: fit-content;
    position: absolute;
    opacity: 0.5;
    z-index: 1;
    width: 400px;
    height: 500px;
    animation: arrow 15s infinite linear alternate;
  }
}

@keyframes arrow {
  0% {
    transform: rotate(-10deg) translateX(350px) translateY(-65px);
  }
  100% {
    transform: rotate(-3deg) translateX(310px) translateY(-65px);
  }
}

.notFound {
  width: 100%;
}

.notFound--code {
  font-size: 10em;
}

.notFound--title {
  font-size: 2.5em;
}

.notFound--info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: var(--text);
  text-align: center;
}

.notFound--info * {
  transition: color 0.4s, text-shadow 0.5s;
}

.notFound--info h1 div:first-child {
  font-size: 7em;
}

.notFound--info h1 div:last-child {
  font-size: 2em;
}

.errCode {
  transition: text-shadow 0.6s !important;
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  2% {
    transform: rotate(-5deg);
  }
  5% {
    transform: rotate(5deg);
  }
  10% {
    transform: rotate(0deg);
  }
}

.project {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1000px;
  margin: -5px;
  margin-bottom: 50px;
}

.project__block {
  box-shadow: 0 0 5px 2px var(--base-special);
  background-color: var(--base);
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  flex: 1 1 calc(100% / 3);
  min-height: 400px;
  max-width: 500px;
  min-width: 250px;
  border-radius: 5px;
}

.project__block * {
  transition: all 0.5s;
  color: var(--text);
}

.project__block__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--base-special-o);
  width: 100%;
}

.project__block__header--title {
  font-size: 2em;
  font-weight: bold;
  padding: 5px;
  animation: hi 0.5s;
}

.project__block__header--tech {
  width: 80%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 20px;
  color: var(--text-special);
}

.project__block__header--tech span {
  color: var(--text-special);
  font-weight: bold;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 40px;
  font-size: 17px;
  padding: 0 10px 0 10px;
  margin: 2px;
}

.project__block__content {
  opacity: 0.8;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.project__block__content--transcript {
  opacity: 0.9;
  padding: 5px;
  font-size: 1.1em;
  text-shadow: 0 0 1px var(--text-shadow);
}

.project__block__footer {
  display: flex;
  width: 100%;
  justify-content: space-around;
}

.project__block a {
  text-decoration: none;
  color: var(--text-special);
  font-weight: bold;
  font-size: calc(90% + 10px);
  padding: 10px;
  text-align: center;
  width: 100%;
  text-shadow: 0 0 1px var(--text);
}

.project__block:hover {
  box-shadow: 0 0 5px var(--base-shadow);
}

.project__block:hover .project__block__header--tech {
  opacity: 1;
}

.project__block:hover .project__block__content--transcript {
  font-size: 1.1em;
  opacity: 1;
}

.project__block:hover a {
  animation: rotate 2s infinite 0.5s;
}

.project__block:hover a:hover {
  animation: unset;
  transform: scale(1.2, 1.2);
}

.project__block:hover a:hover:after {
  content: " -";
}

.project__block:hover a:hover:before {
  content: "- ";
}

.project__block:hover a:nth-child(2) {
  animation-delay: 1s;
}

@media (pointer: none), (pointer: coarse) {
  .project__block .project__block__header--tech {
    text-shadow: 0 0 1px var(--text-shadow);
  }
  .project__block .project__block__content--transcript {
    text-shadow: 0 0 1px var(--text-shadow);
  }
  .project__block a {
    animation: rotate 2s infinite 0.5s;
    text-shadow: 0 0 1px var(--text-special);
    opacity: 1;
  }
  .project__block a:nth-child(2) {
    animation-delay: 1s;
  }
}

@keyframes hi {
  0% {
    color: var(--text);
    text-shadow: -5px -5px var(--text-shadow);
    filter: blur(4px);
  }
  50% {
    color: base(--red);
    filter: sepia(100%);
  }
  100% {
  }
}

.container {
  position: relative;
  z-index: 1;
  margin: 10px;
  margin-top: 90px;
  display: flex;
  max-width: 100%;
  align-items: center;
  flex-direction: column;
  align-self: center;
  justify-content: center;
  transition: opacity 1s;
}

@media screen and (max-width: 799px) {
  .container--blur {
    opacity: 0.2;
  }
}

.sidebar {
  transition: left 1s;
  position: absolute;
  background-color: var(--base);
  width: 100%;
  max-width: 400px;
  left: -400px;
  z-index: 8;
  height: 100%;
  top: 60px;
  display: flex;
  flex-direction: column;
  max-height: 100vh;
}

.sidebar img {
  height: 100%;
  padding: 30px;
  opacity: 0.3;
  margin-bottom: 10vh;
  filter: blur(1px);
}

.sidebar--button {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: var(--text);
  font-weight: bold;
  font-size: 2em;
  width: 100%;
  height: 300px;
  border-bottom: 4px solid var(--base-special-o);
}

.sidebar--active {
  background-color: var(--base-special-o);
}

@media screen and (max-width: 799px) {
  .sidebar--open {
    left: 0px;
  }
}

.svg-tech {
  opacity: 0.3;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 0;
  max-width: 100%;
  filter: blur(2px);
  user-select: none;
}

.svg-footer {
  position: absolute;
  bottom: 0;
  opacity: 0.2;
  z-index: 0;
  max-width: 100%;
  filter: blur(5px);
  user-select: none;
}
